import React from "react";
import s from "./Dialogs.module.css";
import DialogItem from "./Dialogitem/Dialogsitem"
import Message from "./Message/Message"
import {Field, form, reduxForm, reset} from "redux-form";
import {Textarea} from "../common/FormsContols/FormsControls";
import {maxLengthCreator, required} from "../../utils/validators/validators";


class Dialogs extends React.Component {
	
	state = this.props.dialogsPage;
	
	addNewMessage = (values) => {
		this.props.sendMessage(values.newMessageBody);
	};
	
	render() {
		return (
		  <div className={s.dialogs}>
			  <div className={s.dialogs_items}>
				  {this.state.dialogs.map(d => <DialogItem
					 name={d.name}
					 id={d.id}
					 ava={d.ava}
					 key={d.id} /> )}
			  </div>
			  <div className={s.messages}>
				  <div>
					  {this.props.dialogsPage.messages.map (m => <Message
					 message={m.message}
					 messageMe={m.messageMe}
					 key={m.id} /> )}
				  </div>
				  <div>
					  <AddMessageFormRedux onSubmit={this.addNewMessage}/>
				  </div>
			  </div>
		  </div>
		);
	}
}

const maxLength50 = maxLengthCreator(50);

const AddMessageForm = (props) => {
	return (
	  <form onSubmit={props.handleSubmit}>
		  <div className={s.newMessage}>
			  <div>
				  <Field component={Textarea} name="newMessageBody"
							validate={[required, maxLength50]}
							placeholder="Enter your message" />
			  </div>
			  <div>
				  <button>Send</button>
			  </div>
		  </div>
		  
	  </form>
	)
};

const afterSubmit = (result, dispatch) =>
  dispatch(reset('dialogAddMessageForm'));

const AddMessageFormRedux = reduxForm({form: "dialogAddMessageForm", onSubmitSuccess: afterSubmit})(AddMessageForm);

export default Dialogs;